form{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0.5vw;
    padding: 4vw;
    height: 65vh;
}
input{
    width: 20vw;
    height: 2vw;
    text-align: center;
}
.signUp-btn{
    background-color: rgb(255, 153, 0);
    border-radius: 1vw;
    border: 1px solid yellow;
    width: 120px;
    height: 50px;
    color: white;
}
.signUp-btn:hover{
    color: rgb(255, 153, 0);
    background-color: white;
}