.products-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 4px auto;
    gap: 1vw;
}
.product{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 415px;
    
}
.product-image{
    width:10vw;
    height:10vw;
}
.product-title{
    font-size: 1vw;
    font-weight: 0.8vw;
    text-align: center;
    width: 20vw;
}
.product-description{
    padding: 2px 1vw;
    font-size: 0.8vw;
    width: 20vw;
}
.product-img-bg{
    background-image: radial-gradient(white, white,rgb(0, 229, 255));
    display: flex;
    align-items: center;
    justify-content: center;
    width:20vw;
    height:20vw;
    border-radius: 2vw;
}
.product-price{
    color: green;
    font-size: 2vw;
}
.product-rating{
    color: rgb(255, 230, 0);
    font-size: 1.3vw;
    padding-bottom: 1vw;
}
.add-to-cart-btn{
    background-color: rgb(255, 153, 0);
    border-radius: 1vw;
    border: 1px solid yellow;
    width: 120px;
    height: 50px;
    color: white;
}
.add-to-cart-btn:hover{
    color: rgb(255, 153, 0);
    background-color: white;
}