.cart-items{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 4px;
    gap: 1vw;
}
.cart-container{
    display: flex;
    justify-content: space-between;
    padding: 0px 2vw;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 4px;
    gap: 1vw;
}
.product-image{
    width:10vw;
    height:10vw;
}
.product-title{
    font-size: 1vw;
    font-weight: 0.8vw;
    text-align: center;
    width: 20vw;
}
.product-img-bg{
    background-image: radial-gradient(white, white,rgb(0, 229, 255));
    display: flex;
    align-items: center;
    justify-content: center;
    width:20vw;
    height:20vw;
    border-radius: 2vw;
}
.product-price{
    color: green;
    font-size: 2vw;
}

.add-to-cart-btn{
    background-color: rgb(255, 85, 0);
    border-radius: 1vw;
    border: 1px solid red;
    width: 120px;
    height: 50px;
    color: white;
    margin: 5px;
}
.add-to-cart-btn:hover{
    color: rgb(255, 85, 0);
    background-color: white;
}
.item-count-inc{
    background-color: red;
    border-radius: 8vw;
    border: 1px solid red;
    width: 32px;
    height: 26px;
    color: white;
    margin: 5px;
}
.item-count-inc:hover{
    color: white;
    background-color: red;
}
.item-count-dec{
    background-color: red;
    border-radius: 8vw;
    border: 1px solid red;
    width: 32px;
    height: 26px;
    color: white;
    margin: 5px;
}
.item-count-dec:hover{
    color: white;
    background-color: red;
}
.item-count{
    border: 1px solid #000000;
    padding: 7px;
    border-radius: 3vw;
    width: 56px;
    height: 8px;
    font-size: 20px;
}
.checkout{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    font-size: 2vw;
    gap:3px;
    height: 2vh;
}
.DC{
    color: green;
}
.price{
    font-weight: bolder;
}
.leftpart{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 50vw;
    gap: 6vw;
}
.checkout-btn{
    background-color: red;
    color: white;
    padding: 1vw;
    border: none;
    border-radius: 1vw;
    margin: 5px;
    font-size: 1vw;
}
.checkout-btn:hover{
    background-color: white;
    color: red;
    border:1px solid red;
}