.navbar{
    display: flex;
    justify-content: space-between;
    padding: 0px 1vw;
    align-items: center;
    margin: 0px 50px;
    margin-bottom: 40px;
    margin-top: 8px;
    position: sticky;
    top: 0;
    background-color: aliceblue;
    height: 4vw;
    border-radius: 3vw;
}
.nav-logo{
    width: 8vw;
    height: 2vw;
}
a{
    text-decoration: none;
    color: #2b2d38;
    padding: 1vw;
    border-radius: 1vw;
}
a:hover{
    color: white;
    background-color: #2b2d38;
}
.nav-links{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2vw;
}